import React from "react";

export default function Navigation({
  card,
  handleMostPopular,
  handleMostRecent,
}) {
  function mostPopularClick() {
    handleMostPopular(card);
  }

  function mostRecentClick() {
    handleMostRecent(card);
  }
  return (
    <section className="navigation">
      <button className="navigation__button" onClick={mostPopularClick}>
        Most Popular
      </button>
      <button className="navigation__button" onClick={mostRecentClick}>
        Most Recent
      </button>
    </section>
  );
}
